import React, { useState } from 'react';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import {
	KntButton,
	ListHeader,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import { ClientDetailsUser } from 'shared/typings/client.types';
import {
	useEntityActionPopup,
	useLoadAll,
	useNotistack,
	usePageChangeAfterEntityDelete,
	usePagination,
	useSearchQuery,
	useWhiteModal
} from 'shared/hooks';
import { ClientUserInviteForm } from './ClientUserInviteForm';
import { StatusTag } from 'shared/components/DataDisplay';
import { formRequest } from 'modules/entity/actions';
import { useDispatch } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

type Props = {
	clientId: number;
};

export function ClientdtUsers({ clientId }: Props) {
	const { companyId, isPermitted, isAdmin } = useAppState();
	const { page, handlePage } = usePagination();
	const { query, setQuery, searchQuery } = useSearchQuery();

	const { open, modalHandler, WhiteModal } = useWhiteModal();
	const [resendUser, setResendUser] = useState<any>({});

	const dispatch = useDispatch();
	const { t } = useTranslation();

	const { reloadEntityAll } = useLoadAll();
	const { showNotification } = useNotistack();

	const {
		showVisible: showResendConfirmModalVisible,
		visible: resendConfirmModalVisible,
		closePopup: closeResendConfirmModal,
		loading: resendRequestLoading,
		toggleLoading: toggleResendRequestLoading
	} = useEntityActionPopup();

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<any>();
	const deleteClientHandler = () => () => handleClientDelete(deleteVisible!);

	const setQueryHandler = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
		setQuery(event.target.value);

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'client-users',
		entityName: `AllClientUser-${clientId}`
	});

	function handleClientDelete(user: ClientDetailsUser) {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'client-users',
				name: `AllClientUser-${clientId}`,
				method: 'delete',
				url: `/${companyId}/my/company/clients/${clientId}/member/${user.id}/remove/`,
				deleteData: true,
				id: user.id,
				cb: {
					success: () => {
						forwardToPreviousPage();
						showNotification({ message: t('successfully_deleted'), variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: err?.response?.data?.message, variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	}

	function resendInvitationRequest() {
		toggleResendRequestLoading();
		dispatch(
			formRequest({
				entity: 'client-users',
				method: 'patch',
				name: `AllClientUser-${clientId}`,
				url: `/${companyId}/my/company/clients/${clientId}/member/${resendUser?.id}/`,
				params: {},
				values: {},
				cb: {
					success: (data) => {
						reloadEntityAll({
							entity: 'client-users',
							name: `AllClientUser-${clientId}`,
							params: {
								page,
								limit: 5
							},
							url: `/${companyId}/my/company/clients/${clientId}/employee/`
						});
						showNotification({ message: 'Invitation resent successfully', variant: 'success' });
						closeResendConfirmModal();
					},
					error: (error) => {
						showNotification({ message: t('something_went_wrong'), variant: 'error' });
						console.log(error, 'client invitation resend error');
					},
					finally: () => {
						toggleResendRequestLoading();
					}
				},
				primaryKey: 'id'
			})
		);
	}

	function handleResendInvitationClick(user: ClientDetailsUser) {
		showResendConfirmModalVisible(true);
		setResendUser(user);
	}

	function handleResendConfirmModalClicked() {
		resendInvitationRequest();
	}

	return (
		<div className="relative mb-8">
			<ListHeader
				title={t('client_Users')}
				query={query}
				placeholder={t('search_for_users')}
				handleSearch={setQueryHandler}
				btnHandler={modalHandler}
				btnText={t('invite_new_client')}
				isPermitted={isPermitted}
			/>
			<EntityContainer.All
				entity="client-users"
				name={`AllClientUser-${clientId}`}
				url={`/${companyId}/my/company/clients/${clientId}/employee/`}
				params={{
					page,
					limit: 5,
					extra: {
						search: searchQuery
					}
				}}
			>
				{({ items, isFetched, meta }) => {
					// const users = items as C
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '30%' }}>
														{t('full_name')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>
														{t('email')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>
														{t('phone_number')}
													</StyledTableHeadCell>
													{/*<StyledTableHeadCell style={{ width: '10%' }}>Position</StyledTableHeadCell>*/}
													<StyledTableHeadCell style={{ width: '10%' }}>
														{t('role')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														{t('status')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														{t('actions')}
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{(items as ClientDetailsUser[]).map((user) => {
													const { first_name, last_name, roles, email, mobile, status, id, state } =
														user;
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>{`${first_name} ${last_name}`}</StyledTableCell>
															<StyledTableCell>
																<div className="truncate">{email}</div>
															</StyledTableCell>
															<StyledTableCell>{mobile}</StyledTableCell>
															{/*<StyledTableCell>Position</StyledTableCell>*/}
															<StyledTableCell>
																{state === 'Pending' ? (
																	<KntButton
																		isDisabled={!isAdmin}
																		color="primary"
																		onClick={() => handleResendInvitationClick(user)}
																	>
																		{t('resend')}
																	</KntButton>
																) : (
																	t(convertToLocalizationKey(roles?.[0]))
																)}
															</StyledTableCell>
															<StyledTableCell>
																<StatusTag status={state}>{state}</StatusTag>
															</StyledTableCell>
															<StyledTableCell>
																<ActionsButtons.DeleteButton
																	isDisabled={!isAdmin}
																	clickHandler={() => showDeleteVisible(user)}
																/>
															</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
									</TableContainer>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
									<WhiteModal open={open} handleModalOpen={modalHandler}>
										<ClientUserInviteForm onSuccess={modalHandler} clientId={clientId} />
									</WhiteModal>
									<ConfirmModal
										isOpen={Boolean(resendConfirmModalVisible)}
										toggleHandler={closeResendConfirmModal}
										title="Are you sure to resend the request?"
										featureHandler={handleResendConfirmModalClicked}
										isLoading={resendRequestLoading}
										hasLoader
									/>
									<ConfirmModal
										featureHandler={deleteClientHandler()}
										isOpen={Boolean(deleteVisible)}
										toggleHandler={closeDeletePopup}
										title={`Are you sure you want to delete the following staff member: "${deleteVisible?.first_name} ${deleteVisible?.last_name}"`}
										hasLoader
										isLoading={deleteLoading}
									/>
								</>
							)}
						</>
					);
				}}
			</EntityContainer.All>
		</div>
	);
}
