import React from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useLoadAll, useNotistack } from 'shared/hooks';
import { FormHeader, Loader } from 'shared/components';
import { Field } from 'formik';
import { KntInputField, KntPhoneInputField } from 'shared/components/Fields';
import { InviteClientFormFooter } from '../InviteClientFormFooter';
import { useTranslation } from 'react-i18next';

type Props = {
	onSuccess: () => void;
	clientId: number;
};

export function ClientUserInviteForm({ onSuccess, clientId }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { reloadEntityAll } = useLoadAll();
	const { t } = useTranslation();

	return (
		<>
			<EntityForm.Main
				entity="clients"
				name="AllClients"
				url={`/${companyId}/clients/${clientId}/employees/`}
				method="post"
				primaryKey="id"
				sendAsFormData={false}
				// normalizeData={(data: any) => data}
				// prependData
				fields={[
					{
						name: 'first_name',
						required: true,
						value: '',
						onSubmitKey: ''
					},
					{
						name: 'last_name',
						required: true,
						value: '',
						onSubmitKey: ''
					},
					{
						name: 'email',
						onSubmitKey: '',
						required: true,
						type: 'email',
						value: ''
					},
					{
						name: 'position',
						onSubmitKey: '',
						required: false,
						value: null
					},
					{
						name: 'mobile',
						onSubmitKey: '',
						required: true,
						value: ''
					},
					{
						name: 'is_admin',
						onSubmitKey: '',
						value: false
					}
				]}
				onSuccess={(response, resetForm): any => {
					showNotification({ message: 'Success! Waiting for client accepting..' });
					reloadEntityAll({
						entity: 'client-users',
						name: `AllClientUser-${clientId}`,
						params: {
							page: 1,
							limit: 10
						},
						url: `/${companyId}/my/company/clients/${clientId}/employee/`
					});
					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					console.log(error);
					const errorText = error?.response?.data?.message || t('something_went_wrong');
					showNotification({ message: errorText, variant: 'error' });
				}}
			>
				{({ isSubmitting, values, setFieldValue, resetForm }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('client user invite  values', values);
					}
					return (
						<div>
							<FormHeader resetForm={resetForm} title="Invite Business Client User" />
							<>
								<Field
									label="First Name"
									labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
									name="first_name"
									component={KntInputField}
									placeholder="Type your firstname.."
									required
									autoComplete="off"
								/>
								<Field
									label="Last Name"
									labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
									name="last_name"
									component={KntInputField}
									placeholder="Type your lastname.."
									required
									autoComplete="off"
								/>
								<Field
									type="email"
									label="Email"
									labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
									name="email"
									component={KntInputField}
									placeholder="Type your email.."
									required
								/>
								<Field
									label="Phone"
									name="mobile"
									component={KntPhoneInputField}
									placeholder="Enter company mobile phone number"
									enableSearch
									required
									labelClasses="!text-xs !ml-6 !text-kgrey-welcome"
									onChangeHandler={setFieldValue}
								/>
								<Field
									label="Position"
									labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
									name="position"
									component={KntInputField}
									placeholder="Type client user position.."
									autoComplete="off"
								/>
								<div className={'flex items-center justify-end'}>
									{isSubmitting && <Loader tiled withOverlay />}
									<InviteClientFormFooter
										onSuccess={onSuccess}
										isSubmitting={isSubmitting}
										submitText="Invite Client User"
									/>
								</div>
							</>
						</div>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
