import React from 'react';
import { ClientCompany } from 'shared/typings/client.types';
import { AvatarSized, DetailsInfo } from 'shared/components/Elements';
import { getClientAvatarUrl, getClientName } from '../../utils';
import { HiHome } from 'react-icons/hi';
import { FaUserCog, FaUsers } from 'react-icons/fa';
import { IconClientUsers } from 'shared/icons';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

type Props = {
	clientCompany: ClientCompany;
	isIndividual: boolean;
};

export function ClientdtHeader({ clientCompany, isIndividual }: Props) {
	const { t } = useTranslation();
	const clientName = getClientName(isIndividual, clientCompany.name, clientCompany.owner);
	return (
		<div className="mb-8 flex flex-wrap items-center">
			<div className="py-4">
				<AvatarSized
					size={108}
					letter={clientName[0]}
					alt={clientName}
					src={getClientAvatarUrl(isIndividual, clientCompany.icon, clientCompany.owner)}
					className="!mr-8"
				/>
			</div>
			<DetailsInfo
				text={t(convertToLocalizationKey(clientCompany.status))}
				title={t('status')}
				wrapperClassnames="bg-kgrey-bgmenu"
				textClassName="!text-lg font-semibold"
			>
				<HiHome className="text-kgrey" />
			</DetailsInfo>
			<DetailsInfo
				text={isIndividual ? t('individual') : t('business')}
				title={t('client_type')}
				wrapperClassnames="bg-kgrey-bgmenu"
				textClassName="!text-lg font-semibold"
			>
				<FaUserCog className="text-kgrey" />
			</DetailsInfo>
			{!isIndividual ? (
				<>
					<DetailsInfo
						text={clientCompany.users_count}
						title={t('client_user_count')}
						wrapperClassnames="bg-kgrey-bgmenu"
						textClassName="!text-lg font-semibold"
					>
						<IconClientUsers className="text-kgrey" />
					</DetailsInfo>
				</>
			) : null}
			<DetailsInfo
				text={clientName}
				title={isIndividual ? t('client_name') : t('business_name')}
				wrapperClassnames="bg-kgrey-bgmenu"
				textClassName="!text-lg font-semibold"
			>
				<FaUsers className="text-kgrey" />
			</DetailsInfo>
		</div>
	);
}
