import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import {
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow,
	AsyncSelect,
	ActionsButtons,
	ConfirmModal
} from 'shared/components';
import {
	ListHeader,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import { LinkedClientEntity, LinkedClientRoleInList } from 'shared/typings/client.types';
import { usePagination, useSearchQuery, useWhiteModal } from 'shared/hooks';
import get from 'lodash/get';
import { useLinkedClient } from '../../hooks';
import { CreateLinkedClientForm } from './CreateLinkedClientForm';
import { KntTwilioStyle } from 'shared/services';
import { useTranslation } from 'react-i18next';

type Props = {
	clientId: number;
};

export function ClientdtLinkedClients({ clientId }: Props) {
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { query, setQuery, searchQuery } = useSearchQuery();
	const { open, modalHandler, WhiteModal } = useWhiteModal();
	const { t } = useTranslation();

	const {
		handleRoleUpdate,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		handleLinkedClientDelete
	} = useLinkedClient();

	const setQueryHandler = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
		setQuery(event.target.value);

	const deleteClientHandler = () => () => handleLinkedClientDelete(deleteVisible!);

	return (
		<div className="relative mb-8">
			<ListHeader
				title={t('linked_clients')}
				query={query}
				isPermitted={isPermitted}
				placeholder={t('search_for_linked_clients')}
				handleSearch={setQueryHandler}
				btnHandler={modalHandler}
				btnText={t('new_linked_client')}
			/>
			{companyId ? (
				<EntityContainer.All
					entity="linked-clients"
					name="AllLinkedClients"
					url={`/${companyId}/my/company/linked_client/`}
					params={{
						page,
						limit: 5,
						extra: {
							search: searchQuery,
							to_company: clientId.toString()
						}
					}}
				>
					{({ items, isFetched, meta }) => {
						return (
							<>
								{!isFetched ? (
									<Loader tiled withOverlay />
								) : (
									<>
										<TableContainer className="!overflow-visible" classes={{ root: 'relative ' }}>
											<StyledTable
												aria-label="simple table"
												classes={{ root: 'table-fixed dark-mode-styled-table' }}
											>
												<TableHead>
													<StyledTableRow>
														<StyledTableHeadCell style={{ width: '22%' }}>
															{t('client_name')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '10%' }}>
															{t('client_type')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '22%' }}>
															{t('email')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '13%' }}>
															{t('phone_number')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '25%' }}>
															{t('role')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '8%' }}>
															{t('actions')}
														</StyledTableHeadCell>
													</StyledTableRow>
												</TableHead>
												<TableBody>
													{(items as LinkedClientEntity[]).map((linkedClient) => {
														const { id = undefined } = linkedClient;

														const fn = get(linkedClient, 'from_company_details.first_name');
														const ln = get(linkedClient, 'from_company_details.last_name');
														const email = get(linkedClient, 'from_company_details.username');
														const phone = get(linkedClient, 'from_company_details.mobile');
														const isIndividual = get(linkedClient, 'from_company_details');
														// const avatar = get(
														// 	linkedClient,
														// 	'from_company_details.avatar.thumbnail_150'
														// );
														return (
															<StyledTableRow key={id}>
																<StyledTableCell>
																	{fn ? `${fn} ${ln}` : get(linkedClient, 'from_company.name', '')}
																</StyledTableCell>
																<StyledTableCell>
																	{isIndividual ? t('individual') : t('business')}
																</StyledTableCell>
																<StyledTableCell>
																	<div className="truncate">{email ? email : 'No email'}</div>
																</StyledTableCell>
																<StyledTableCell>
																	{phone ? phone : get(linkedClient, 'from_company.phone', '')}
																</StyledTableCell>
																<StyledTableCell>
																	<div className="react-select-dropdown">
																		{/* @ts-ignore */}
																		<AsyncSelect
																			name="route"
																			customStylesProps={KntTwilioStyle}
																			placeholder="Choose role here"
																			optionLabel="name"
																			optionValue="id"
																			loadOptionsUrl={`/${companyId}/my/company/client/roles/`}
																			loadOptionsParams={(search: string) => ({
																				extra: {
																					search
																				}
																			})}
																			value={linkedClient.role}
																			loadOptionsKey="results"
																			isSearchable={true}
																			isClearable={true}
																			onChange={(option: LinkedClientRoleInList) => {
																				handleRoleUpdate(option, linkedClient);
																			}}
																		/>
																	</div>
																</StyledTableCell>
																<StyledTableCell>
																	<div className="flex-centered">
																		<ActionsButtons.DeleteButton
																			isDisabled={!isPermitted}
																			clickHandler={() => showDeleteVisible(linkedClient)}
																		/>
																	</div>
																</StyledTableCell>
															</StyledTableRow>
														);
													})}
												</TableBody>
											</StyledTable>
										</TableContainer>
										<div className="news__pagination my-4">
											{items.length ? (
												<Pagination
													count={meta?.last_page}
													page={page ? +page : 1}
													defaultPage={6}
													siblingCount={1}
													color={'primary'}
													onChange={handlePage}
												/>
											) : null}
										</div>

										{/*Create Linked Client Form*/}
										<WhiteModal open={open} handleModalOpen={modalHandler}>
											<CreateLinkedClientForm onSuccess={modalHandler} clientId={clientId} />
										</WhiteModal>

										{/*Linked Client Delete Popup*/}
										<ConfirmModal
											featureHandler={deleteClientHandler()}
											isOpen={Boolean(deleteVisible)}
											toggleHandler={closeDeletePopup}
											title={`Are You Sure To Delete Linked Client?`}
											hasLoader
											isLoading={deleteLoading}
										/>
									</>
								)}
							</>
						);
					}}
				</EntityContainer.All>
			) : null}
		</div>
	);
}
