import clsx from 'clsx';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

type StatusTagProps = {
	status: string | 'Pending' | 'Logged';
	children: React.ReactNode | string;
};

export function StatusTag({ status, children }: StatusTagProps) {
	const { t } = useTranslation();
	return (
		<span
			className={clsx(
				'inline-block rounded-4xl bg-primary py-0.5 px-5 text-10px uppercase leading-4 text-white',
				{
					'bg-kgrey-inputBg text-kdark': status === 'Pending',
					'bg-primary-dark': status === 'Logged'
				}
			)}
		>
			{children !== null ? t(convertToLocalizationKey(String(children))) : t('accepted')}
		</span>
	);
}
