import React from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import { FormHeader, Loader } from 'shared/components';
import get from 'lodash/get';
import { LinkedClientFormElements } from './LinkedClientFormElements';
import { useTranslation } from 'react-i18next';

type Props = {
	onSuccess: () => void;
	clientId: number;
};

export function CreateLinkedClientForm({ onSuccess, clientId }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { t } = useTranslation();

	return (
		<>
			<EntityForm.Main
				entity="linked-clients"
				name="AllLinkedClients"
				url={`/${companyId}/my/company/linked_client/`}
				method="post"
				primaryKey="id"
				sendAsFormData={false}
				normalizeData={(data: any) => data}
				prependData
				fields={[
					{
						name: 'role',
						onSubmitValue: (value) => get(value, 'id'),
						onSubmitKey: '',
						required: true,
						type: 'object'
					},
					{
						name: 'from_company',
						onSubmitKey: ''
					},
					{
						name: 'to_company',
						value: Number(clientId),
						onSubmitKey: ''
					}
				]}
				onSuccess={(response, resetForm): any => {
					showNotification({ message: t('successfully_created') });
					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					const errorText = error?.response?.data?.message || t('something_went_wrong');
					showNotification({ message: errorText, variant: 'error' });
				}}
			>
				{({ isSubmitting, values, setFieldValue, resetForm }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('linked user invite  values', values);
					}
					return (
						<div>
							<FormHeader resetForm={resetForm} title={t('search_client_to_link')} isEditing />
							<>
								<div className={'flex items-center'}>
									<LinkedClientFormElements
										setFieldValue={setFieldValue}
										isSubmitting={isSubmitting}
										onSuccess={onSuccess}
									/>
									{isSubmitting && <Loader tiled withOverlay />}
								</div>
							</>
						</div>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
