import config from 'config';
import React, { useState } from 'react';
import { useDidMountEffect } from 'shared/hooks';
import get from 'lodash/get';
import { Field } from 'formik';
import { useAppState } from 'shared/state';
import { AsyncSelect as AsyncSelectComp } from 'shared/components';
import { InviteClientFormFooter } from '../InviteClientFormFooter';
import { KntTwilioStyle } from 'shared/services';
import { KntInput } from 'shared/components/Fields';
import { AvatarSized } from 'shared/components/Elements';
import AsyncSelectJs from 'shared/components/Fields/AsyncSelectJs';
import isEmpty from 'lodash/isEmpty';
import { useTranslation } from 'react-i18next';

const imgPath = `${config.PUBLIC_ASSETS}img/`;

type Props = {
	setFieldValue: any;
	onSuccess: () => void;
	isSubmitting: boolean;
};

export function LinkedClientFormElements({ setFieldValue, onSuccess, isSubmitting }: Props) {
	const { companyId } = useAppState();
	const [state, setState] = useState({
		firstname: '',
		lastname: '',
		phone: '',
		email: '',
		avatar: `${imgPath}avatar-placeholder.png`,
		is_individual: true,
		businessName: null
	});
	const { firstname, lastname, phone, email, avatar, is_individual, businessName } = state;
	const [client, setClient] = useState<any>({});
	const { t } = useTranslation();

	useDidMountEffect(() => {
		const is_individual = get(client, 'is_individual');
		const fn = get(client, 'owner.first_name');
		const ln = get(client, 'owner.last_name');
		const phone = get(client, 'owner.mobile');
		const email = get(client, 'owner.username');
		const avatar = get(client, 'owner.avatar.thumbnail_150');

		setState((prev) => ({
			...prev,
			firstname: fn ? fn : 'No Firstname',
			lastname: ln ? ln : 'No Lastname',
			phone: is_individual ? phone : client.phone || 'No Phone',
			email: email ? email : 'No Email',
			avatar: avatar ? avatar : null,
			is_individual,
			businessName: is_individual ? null : get(client, 'name')
		}));
		return () => {
			setClient({});
		};
	}, [client]);

	const handleClientChange = (option: any) => {
		if (option) {
			setFieldValue('from_company', option.id);
			setClient(option);
		} else {
			setClient({});
		}
	};

	const isClientSelected = !isEmpty(client);

	return (
		<div className="react-select-dropdown w-full">
			<div className="react-select-dropdown mb-4">
				{/*@ts-ignore*/}
				<AsyncSelectComp
					name="route"
					customStylesProps={KntTwilioStyle}
					placeholder={t('select_client_to_link')}
					optionLabel={(option: any) => {
						if (get(option, 'first_name') && get(option, 'last_name')) {
							return `${get(option, 'first_name')} ${get(option, 'last_name')} (${get(
								option,
								'name'
							)}) ${
								get(option, 'is_individual') ? ` - ${t('individual')}` : ` - ${t('business')}`
							}`;
						} else {
							return `${get(option, 'name')} ${
								get(option, 'is_individual') ? ` - ${t('individual')}` : ` - ${t('business')}`
							}`;
						}
					}}
					optionValue="id"
					loadOptionsUrl={`/${companyId}/client_companies/`}
					loadOptionsParams={(search: string) => ({
						extra: {
							search
						}
					})}
					loadOptionsKey="results"
					isSearchable={true}
					isClearable={true}
					onChange={handleClientChange}
				/>
			</div>
			{isClientSelected ? (
				<Field
					component={AsyncSelectJs}
					customStylesProps={KntTwilioStyle}
					name="role"
					placeholder={t('select_role')}
					optionLabel="name"
					optionValue="id"
					isSearchable
					isClearable={false}
					loadOptionsKey="results"
					loadOptionsUrl={`/${companyId}/my/company/client/roles/`}
					loadOptionsParams={(search: string) => ({
						extra: {
							search
						}
					})}
				/>
			) : null}
			{!is_individual && isClientSelected ? (
				<div className="mt-4">
					<KntInput
						name="businessName"
						placeholder="Business name"
						label={t('business_name')}
						onChange={() => {}}
						value={businessName as unknown as string}
						error={false}
						helperText=""
						disabled
					/>
				</div>
			) : null}
			{is_individual && isClientSelected ? (
				<div className="mt-4">
					<KntInput
						name="fullName"
						placeholder="Full name"
						label={t('full_name')}
						onChange={() => {}}
						value={`${firstname} ${lastname}` as unknown as string}
						error={false}
						helperText=""
						disabled
					/>
				</div>
			) : null}
			{isClientSelected ? (
				<div className="mb-4 flex items-end justify-between">
					<div className="flex-grow">
						<KntInput
							name="phone"
							placeholder="Phone"
							label={t('phone')}
							onChange={() => {}}
							value={phone as unknown as string}
							error={false}
							helperText=""
							disabled
							classNames="!mb-0"
						/>
					</div>
					<AvatarSized
						size={40}
						letter={businessName?.[0] || firstname?.[0]}
						alt={businessName || firstname}
						src={avatar}
						className="ml-4 mb-1"
					/>
				</div>
			) : null}

			{is_individual && isClientSelected ? (
				<div className="mt-4">
					<KntInput
						name="email"
						placeholder="Email"
						label={t('email')}
						onChange={() => {}}
						value={email as unknown as string}
						error={false}
						helperText=""
						disabled
					/>
				</div>
			) : null}

			<div className="flex-center-between mt-8">
				<InviteClientFormFooter
					onSuccess={onSuccess}
					isSubmitting={isSubmitting}
					submitText={t('create')}
					disableOptions={!isClientSelected}
				/>
			</div>
		</div>
	);
}
