import React from 'react';
import { useRouter } from 'shared/hooks';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { Loader } from 'shared/components';
import { ClientdtHeader, ClientdtLinkedClients, ClientdtUsers } from '../../components/ClientView';
import { ClientCompany } from 'shared/typings/client.types';
import { useTranslation } from 'react-i18next';
function ClientDetails() {
	const { params } = useRouter();
	const { companyId } = useAppState();
	const { t } = useTranslation();

	const clientId = parseInt((params as { clientId: string })?.clientId);
	if (!companyId) {
		return null;
	}

	return (
		<div className="py-8">
			<h1 className="mb-6 text-2xl font-semibold dark:text-white">{t('client_details')}</h1>
			<EntityContainer.One
				id={clientId}
				relations={''}
				entity="clients"
				name="AllClients"
				url={`/${companyId}/client_companies/${clientId}/`}
				params={{}}
			>
				{({ item, isFetched }) => {
					const isIndividual = (item as ClientCompany).is_individual;
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<>
									<ClientdtHeader
										clientCompany={item as ClientCompany}
										isIndividual={isIndividual}
									/>
									{/*Business Client Users*/}
									{isIndividual ? null : <ClientdtUsers clientId={(item as ClientCompany).id} />}

									{/*Linked Clients*/}
									<ClientdtLinkedClients clientId={(item as ClientCompany).id} />
								</>
							)}
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}

export default ClientDetails;
